import { Component , OnInit , ViewChild } from '@angular/core';
import {AlertController, ModalController ,  IonRouterOutlet} from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import {  Router, NavigationExtras } from '@angular/router';
import { AuthenticationService } from '../../services/Authentication.service';
import { FormGroup, FormControl } from '@angular/forms';
import { ReplaySubject} from 'rxjs';
import { IonInfiniteScroll } from '@ionic/angular';
import {Plugins, Capacitor,  Geolocation , CallbackID} from '@capacitor/core';


@Component({
selector: 'page-frmordencomponente-list',
templateUrl: 'frmordencomponente-list.page.html',
styleUrls: ['./frmordencomponente-list.page.scss'],
})
export class frmordencomponenteListPage implements OnInit {
@ViewChild(IonInfiniteScroll, { static : false }) infiniteScroll: IonInfiniteScroll;
rangeForm: FormGroup;
searchQuery: string;
showAgeFilter = false; showmenu = false;
items: any  = [] ;
variables: any = [];
userinfo: any = [];
idllave: any;
viewType = 'grid';
tituloxx = '';
efecto = 0;
idformulario:any='';
seguro = '';
keyxxx: any;
geoposiciontxt: any;
msg = '';
apifoto: String;
itemsList: Array<any>;
public currentSegment = 'activa';
searchSubject: ReplaySubject<any> = new ReplaySubject<any>(1);
constructor(  private routerOutlet: IonRouterOutlet , private router: Router, private authenticationService: AuthenticationService,
   public alertController: AlertController,     public translateService: TranslateService    ,    private modalController: ModalController
   ) {  this.apifoto = this.authenticationService.getapifoto();
 }
changeToList() { this.viewType = 'list'; }
changeToGrid() {   this.viewType = 'grid';  }
ngOnInit() {
  this.viewType = 'list';
    this.variables = this.router.getCurrentNavigation().extras.state.variables;
    this.idllave = this.router.getCurrentNavigation().extras.state.idllave;
 this.tituloxx = this.router.getCurrentNavigation().extras.state.title;
this.keyxxx = this.router.getCurrentNavigation().extras.state.keyxxx;
this.idformulario = this.router.getCurrentNavigation().extras.state.idfomulario;


    this.userinfo = this.authenticationService.getcopiauser();



    this.searchQuery = '';
    this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value;  });
    this.translateService.get('MSGM').subscribe((value) => {   this.msg = value;  });
    this.rangeForm = new FormGroup({    dual: new FormControl({lower: 1, upper: 100})   });
    this.currentSegment = 'activa';
    this.pideregistros( this.currentSegment);
  }
  filterContacts(event: any) {
    const selectedCategory = event.detail.value;
    this.currentSegment = selectedCategory;
    this.pideregistros(selectedCategory);
  }
  pideregistros0() {
   this.pideregistros(this.currentSegment);
  }





  pideregistros(op) {
       this.itemsList = [];
       this.efecto = 0 ;
let filtro1 = '';
let filtro2 = '';

       switch (this.keyxxx) {
        case 1:
        filtro1 = this.idllave+ '/trayectoriaorden/' + this.userinfo[0].idempresa._id ;

        filtro2 = 'frmmovils';

        break;
        case 2:
          filtro1 = this.idllave+ '/flujotrayectoriaorden/' + this.userinfo[0].idempresa._id + '°' + this.idformulario ;
  
          filtro2 = 'frmmovils';
  
          break;
          default:
      }

       this.authenticationService.getReg(filtro1,  filtro2, 0).then((data) => {
       this.efecto = 1;
       this.items =  data;
       this.itemsList = this.items;
 }, (err) => {
   this.authenticationService.presentAlert(err.error , this.msg , '');
 });
  }
  searchList() {
    this.searchSubject.next({
     lower: this.rangeForm.controls.dual.value.lower,
     upper: this.rangeForm.controls.dual.value.upper,
     query: this.searchQuery
   });
    const query = (this.searchQuery && this.searchQuery !== null) ? this.searchQuery : '';
    this.itemsList = this.filterList(this.items, query);
  }
 filterList(list, query): Array<any> {
   return list.filter(item => item.nombre.toLowerCase().includes(query.toLowerCase()));
  }


   loadData(event) {
    setTimeout(() => {
      event.target.complete();
      if ( this.items.length === 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }
}
